import React, { FunctionComponent } from "react"
import { Link } from "gatsby"

/**
 * Type
 */
interface ISmartLink extends React.AnchorHTMLAttributes<any> {
  to?: string
}

/**
 * Check link compliance
 * @description Check if the route could be managed by Gatsby system
 */
function isNotGatsbyCompliant(path:string) {
  return path.match(/^(\/\/|www.|http|https|#)/) !== null
}

/**
 * Link
 * @description use native or Gatsby link according the path
 */
const SmartLink:FunctionComponent<ISmartLink> = ({ to, children, ...extra }) => {
  // no link
  if (!!!to) return <></>

  // native link
  if (isNotGatsbyCompliant(to)) return (
    <a href={to} {...extra}>
      {children}
    </a>
  )

  // gatsby link
  return (
    <Link to={to} {...extra}>
      {children}
    </Link>
  )
}

export default SmartLink
