/**
 * List routes used into the project
 * Avoid to write routes and labels into views
 * And so simplify the maintenance
 */
type IRouteRegister = {
  [key:string]: {
    label:string,
    href:string
  }
}

const register:IRouteRegister = {
  home: {
    label: "Accueil",
    href: "/",
  },
  legal: {
    label: "Mentions légales",
    href: "/mentions-legales",
  },
  github: {
    label: "Github",
    href: "https://github.com/AurelienDud",
  },
  linkedin: {
    label: "LinkedIn",
    href: "https://www.linkedin.com/in/aureliendudonney/",
  },
  portfolio: {
    label: "Storybook",
    href: "https://aureliendud.github.io/portfolio/"
  }
};

/**
 * Get route from register
 * @description
 * Queries can be added by setting an array of array
 * queries[[key, value],...]
 */
export const getRoute = (to:string, queries?:string[][]) => {
  const isRegistered = Object.prototype.hasOwnProperty.call(register, to);

  const model = {
    isRegistered: isRegistered,
    label: isRegistered ? register[to].label : undefined,
    href: isRegistered ? register[to].href : undefined,
  }

  if (Array.isArray(queries) && queries.length > 0 && model.href) {
    model.href = model.href.concat("?").concat(
      queries
        .filter((item) => Array.isArray(item))
        .map((item) => item.join("="))
        .join("&")
    );
  }

  return model;
}

/**
 * An helper to manage category route
 */
export const getCategoryRoute = (category:string) => `/categories/${category}`
