import React, { FunctionComponent } from "react"
import { getRoute } from "services/routes"
import Link from "components/link"

/**
 * View
 */
const Footer:FunctionComponent = () => (
  <div className="bg-secondary small text-light py-3 text-center text-md-left">
    <div className="container">
      <div className="row align-items-center justify-content-between">
        <div className="col col-md-auto order-md-2">
          <div className="d-flex flex-column flex-md-row">
            <div className="nav-link">
              <Link 
                to={getRoute("legal").href} 
                className="text-light"
              >
                {getRoute("legal").label}
              </Link>
            </div>
          </div>
        </div>
        <div className="col col-md-auto order-md-1">
          © 2021 Aurélien Dudonney
        </div>
      </div>
    </div>
  </div>
)

export default Footer
