import React, { FunctionComponent } from "react"
import Layout from "partials/layouts/layout"
import Header from "partials/common/header"
import Footer from "partials/common/footer"

export const layoutPageStyleClassName = "str-page pb-3"

/**
 * Type
 */
interface ILayoutPage {
  meta?: Partial<ISEO>
}

/**
 * View
 */
const LayoutPage:FunctionComponent<ILayoutPage> = ({ children, meta }) => (
  <Layout meta={meta}>
    <Header />
    <div className={layoutPageStyleClassName}>
      {children}
    </div>
    <Footer />
  </Layout>
)

export default LayoutPage
