import React, { FunctionComponent } from "react"
import Helmet from "react-helmet"

const defaultValues = {
  type: "website",
  lang: ''
}

type ISEOMeta = Partial<ISEO>

type IMetaList = {
  name?: string
  property?: string,
  content?: string,
}[]

/**
 * Generate metadata
 * @param meta 
 */
const metadataList = (meta:Partial<ISEO>):IMetaList => {
  const metaList:IMetaList = []

  // global meta

  metaList.push({
    name: "description",
    content: meta.description,
  })

  // Open Graph meta
  // https://ogp.me/

  metaList.push({
    property: "og:title",
    content: meta.title || meta.siteTitle,
  })

  metaList.push({
    property: "og:description",
    content: meta.description,
  })
  
  metaList.push({
    property: "og:type",
    content: meta.type || defaultValues.type,
  })

  !!meta.tags && meta.tags?.length > 0 && metaList.push({
    name: "keywords",
    content: meta.tags.join(", "),
  })

  !!meta.tags && meta.tags?.length > 0 && meta.type === "article" && metaList.push({
    name: "article:tag",
    content: meta.tags.join(", "),
  })

  meta.type === "article" && meta.author && metaList.push({
    name: "article:author",
    content: meta.author,
  })

  meta.type === "article" && meta.category && metaList.push({
    name: "article:section",
    content: meta.category,
  })

  // Twitter meta
  // https://developer.twitter.com/en/docs/twitter-for-websites/cards/guides/getting-started
  
  metaList.push({
    name: "twitter:card",
    content: "summary",
  })
  
  metaList.push({
    name: "twitter:creator",
    content: meta.author,
  })
  
  metaList.push({
    name: "twitter:title",
    content: meta.siteTitle,
  })

  metaList.push({
    name: "twitter:description",
    content: meta.description,
  })

  // return full list
  return metaList
}


/**
 * Seo
 * add some SEO elements thanks to React-helmet
 */
const SEOMeta:FunctionComponent<ISEOMeta> = (props) => (
  <Helmet
    htmlAttributes={{ lang: props.lang || defaultValues.lang }}
    title={props.title || props.siteTitle}
    titleTemplate={`%s | ${props.siteTitle}`}
    meta={metadataList(props)}
  />
)

export default SEOMeta
