import React, { FunctionComponent, useState, useEffect } from "react"
import { useStaticQuery, graphql } from "gatsby"
import Link from "components/link"
import { StaticImage } from "gatsby-plugin-image"
import { getRoute } from "services/routes"
import {
  // SiStorybook as StorybookIcon,
  SiGithub as GithubIcon,
  SiLinkedin as LinkedinIcon,
} from "react-icons/si"


// GraphQL query
export const query = graphql`
  query {
    meta: site {
      siteMetadata {
        title
      }
    }
  }
`

// styles
const headerItemStyles = "nav-link font-weight-bold text-current px-2 py-0"
const labelStyles = "ml-1 small text-uppercase d-none d-md-inline"

/**
 * Header
 */
const Header:FunctionComponent = () => {
  const data = useStaticQuery(query)

  // State and effect are used to set the title asynchronously
  // because the restrictions of StaticImage that can't handle props coming from parent or external function
  // => https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-plugin-image/#restrictions-on-using-staticimage
  // useStaticQuery is a function that get data from outside this component so...
  // that works by using directly the value but i prefer to avoid any risks
  const [title, setTitle] = useState("")
  useEffect(() => setTitle(data?.meta?.siteMetadata?.title || ""), [data])

  // render
  return (
    <div className="navbar navbar-expand-lg navbar-dark bg-primary sticky-top">
      <div className="container">
        <Link
          className="navbar-brand p-0"
          to={getRoute("home").href}
          title={getRoute("home").label}
        >
          <StaticImage
            src="../../assets/images/logo.png"
            width={48}
            height={48}
            className="d-inline-block align-top"
            alt={title}
          />
        </Link>

        <div className="d-flex ml-auto">
          {/* <Link
            to={getRoute("portfolio").href}
            title={getRoute("portfolio").label}
            className={headerItemStyles}
            target="_blank"
            rel="noopener"
          >
            <StorybookIcon />
            <span className={labelStyles}>
              {getRoute("portfolio").label}
            </span>
          </Link> */}

          <Link
            to={getRoute("github").href}
            title={getRoute("github").label}
            className={headerItemStyles}
            target="_blank"
            rel="noopener"
          >
            <GithubIcon />
            <span className={labelStyles}>
              {getRoute("github").label}
            </span>
          </Link>
          <Link
            to={getRoute("linkedin").href}
            title={getRoute("linkedin").label}
            className={headerItemStyles}
            target="_blank"
            rel="noopener"
          >
            <LinkedinIcon />
            <span className={labelStyles}>
              {getRoute("linkedin").label}
            </span>
          </Link>
        </div>
      </div>
    </div>
  )
}

export default Header
