import React, { FunctionComponent } from "react"
import { StaticQuery, graphql } from "gatsby"
import "assets/styles/index.scss"
import SEO from "components/seo"

/**
 * Type
 */
interface ILayout {
  meta?: Partial<ISEO>
}

/**
 * SEO params transformer
 */
const seoParams = (data:ISiteMetaQuery, meta?:Partial<ISEO>):Partial<ISEO> => ({
  title: meta?.title,
  siteTitle: data?.site?.siteMetadata?.title,
  description: meta?.description || data?.site?.siteMetadata?.description,
  lang: meta?.lang,
  author: meta?.author || data?.site?.siteMetadata?.author,
  tags: meta?.tags,
  category: meta?.category,
  type: meta?.type,
})

/**
 * Data query
 */
const query = graphql`
  query SiteMetaQuery {
    site {
      siteMetadata {
        title
        description
        siteUrl
        author
        logo
      }
    }
  }
`

/**
 * View
 */
const Layout:FunctionComponent<ILayout> = ({ children, meta }) => (
  <StaticQuery
    query={query}
    render={(data:ISiteMetaQuery) => (
      <>
        <SEO {...seoParams(data, meta)} />
        {children}
      </>
    )}
  />
)

export default Layout
